// styles

.bg-dark {color: white;}

.bg-b1 {
  background-color: $clr-b0;
  background-image:
    linear-gradient(to right, rgba(black,0.5) 0%, rgba(black,0.1) 100%),
    url(../../assets/images/content/patterns/triangles.png);
  background-size:
    cover,
    155px auto;
}

.bg-b2 {
  background-color: $clr-b2;
  background-image:
    linear-gradient(to right, rgba(black,0.5) 0%, rgba(black,0.1) 100%),
    url(../../assets/images/content/patterns/hexagons.png);
  background-size:
    cover,
    168px auto;
}

.bg-b3 {
  background-color: $clr-b3;
  background-image:
    linear-gradient(to right, rgba(black,0.5) 0%, rgba(black,0.1) 100%),
    url(../../assets/images/content/patterns/diamonds.png);
  background-size:
    cover,
    150px auto;
}

.bg-b4 {
  background-color: $clr-b4;
  background-image:
    linear-gradient(to right, rgba(black,0.5) 0%, rgba(black,0.1) 100%),
    url(../../assets/images/content/patterns/triangles.png);
  background-size:
    cover,
    155px auto;
}

.bg-b5 {
  background-color: $clr-4;
  background-image:
    linear-gradient(to right, rgba(white,0.1) 0%, rgba(white,0.1) 100%),
    url(../../assets/images/content/patterns/triangles.png);
  background-size:
    cover,
    155px auto;
}

.bg-ci {
  background-color: #4801AF;
  background-image:
    linear-gradient(to right, rgba(#4801AF,0.75) 0%, rgba(#006C9D,0.75) 100%),
    url(../../assets/images/content/patterns/waves.svg);
  background-size: cover;
  background-position: 50%;
}

// strip

div.strip {
  padding: ($spacing*1.5) 0;
  @include bp-m {padding: ($spacing*2) 0;}

  &.alt {padding-top: 0;}

  &.section {
    padding: ($spacing*2) 0 ($spacing*1);

    @include bp-m {
      padding: ($spacing*3) 0 ($spacing*2);
      &.latest p.lead span {display: block;}
    }

    div.wrap > h2 {@extend .h1;}
    &[class*="bg-b"] p.lead a {color: white;}
    &[class*="bg-b5"] p.lead a {color: $clr-b0;}
  }

  // about success

  // &.about-success {
  //   @include bp-m {text-align: right;}

  //   div.wrap {
  //     position: relative;
  //     z-index: 3;
  //   }
    
  //   ul.checked {      
  //     @include bp-m {
  //       margin-left: auto;

  //       li {
  //         padding-left: 0;
  //         padding-right: 1.4em;
  //         text-align: right;

  //         &:before {left: auto; right: 0;}
  //       }
  //     }

  //     @include bp-l {font-size: $fs-25;}
  //   }

  //   figure {
  //     position: relative;
  //     z-index: 2;
  //     margin: -4rem 0 0;
  //     padding: 27.5% 3rem 0;

  //     @include bp-l {margin-top: -5rem;}
  //     @include bp-m-less {@include hide-screen();}

  //     img {
  //       display: block;
  //       position: absolute;
  //       top: 0; left: 0;
  //     }
  //   }
  // }

  // contact - just for contact pages

   body.contact & {
    @include bp-m {div.row > div:first-child {order: 2;}}
  }

  // call to actions

  &.cta {
    position: relative;
    z-index: 50;
    padding: ($spacing*1.5) 0;
    background-color: $clr-1;
    text-align: center;
    color: white;

    @include bp-m {
      text-align: left;

      &.download {padding: $spacing 0;}
    }

    &.client-feedback {

      div.wrap {
        display: block;

        > h2 {
          margin-bottom: $vmargin;
          font-size: $fs-30;

          @include bp-m {
            @extend .col-4;
            margin: 0 0 -2rem;
          }
        }

        blockquote {
          margin: 0;
          
          @include bp-m {display: flex;}

          div.quote {
            position: relative;
            border-radius: $border-radius;
            padding: $spacing;
            background: rgba(white,0.075);
            font-size: $fs-22;

            @include bp-m {
              @extend .col-8;
              flex: 1 1 auto;
              order: 2;
            }

            &.size-small {font-size: $fs-18;}
            &.size-large {font-size: $fs-25; font-weight: 300;}

            &:after {
              position: absolute;
              top: 100%; left: 50%;
              margin-left: -10px;
              border: 10px solid;
              border-color: rgba(white,0.075) transparent transparent rgba(white,0.075);
              content: "";

              @include bp-m {
                top: auto; left: auto; bottom: 0.7rem; right: 100%;
                border-color: rgba(white,0.075) rgba(white,0.075) transparent transparent;
              }
            }

            >:last-child {margin-bottom: 0;}

            p {
              &:before {content: "\0201C";}
              &:after {content: "\0201D";}
            }
          }

          div.source {
            margin-top: $vmargin;

            @include bp-m {
              @extend .col-4;
              flex: 1 1 auto;
              display: flex;
              align-items: flex-end;
              margin: 0;
            }

            span.initials {
              display: inline-block;
              vertical-align: middle;
              margin: 0 0.75rem;
              border-radius: 3rem;
              width: 2.75rem;
              height: 2.75rem;
              line-height: 2.9rem;
              background: rgba(black,0.5);
              text-decoration: none;
              text-align: center;
              font-size: $fs-22;
              font-weight: 300;
              color: white;

              @include bp-m {
                flex: 0 0 auto;
                order: 2;
                margin-right: 1.4rem;
              }

              &.s2 {background: $clr-b2;}
              &.s3 {background: $clr-b3;}
              &.s4 {background: $clr-b4;}
            }

            p  {
              display: inline-block;
              vertical-align: middle;
              margin: 0;
              text-align: left;
              font-size: $fs-16;

              @include bp-m {
                flex: 1;
                text-align: right;
              }

              span {display: block;}
            }
          }
        }
      }
    }

    div.wrap {

      @include bp-m {
        display: flex;
        justify-content: center;
        align-items: center;
      }

      @include bp-l {align-items: flex-end;}

      > h2 {
        @extend .lead;
        margin: 0;

        @include bp-m {
          flex: 0 1 auto;
          align-self: center;
        }

        > span {
          font-size: $fs-20;
          @include bp-m {display: inline-block;}

          &:before {
            margin: 0 0.4rem 0 0.25rem;
            content: "\02014";
          }         

          span {
            @include bp-s {white-space: nowrap;}
          }
        }
      }

      > p {
        margin: $vmargin auto 0;

        @include bp-m {
          flex: 0 0 auto;
          align-self: center;
          margin: 0 0 0 $spacing;
        }

        a.button {
          @include bp-s-less {display: block;}
        }

        span.other {
          display: block;
          margin-top: 0.75rem;
          font-size: $fs-16;
          text-align: center;

          a {
            color: white;
          }
        }
      }
    }

    @include bp-l {
      &.download,
      &.webinar {

        body.guide &,
        body.form-response div.page-intro + & {margin-top: ($vmargin*2);}
        
        span.image {
          flex: 0 0 auto;
          display: block;
          position: relative;
          width: 280px;
        
          &:before {
            position: absolute;
            left: -($spacing); bottom: -($spacing*1.5);
            padding-top: 69%;
            width: 100%;
            background: no-repeat 50% 100%;
            background-size: 100% auto;
            content: "";
          }
        }

        div.wrap h2 {flex: 1 1 auto;}
      }

      &.download span.image:before {bottom: -($spacing);}

      &.gspr-matcher span.image:before {background-image: url(../../assets/images/content/cta-strips/gspr-matcher.png);}
      &.whitepaper-1 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-1.png);}
      &.whitepaper-2 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-2.png);}
      &.whitepaper-3 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-3.png);}
      &.mdready span.image:before {background-image: url(../../assets/images/content/cta-strips/mdready.png);}
      &.webinar span.image:before {background-image: url(../../assets/images/content/cta-strips/webinar-1.png);}
    }

    &.getting-started {
      padding: ($spacing*2) 0;
      text-align: left;

      @include bp-m {padding: ($spacing*3) 0;}

      div.wrap {
        display: block;

        h2 {
          @extend .h1;
          margin-bottom: $vmargin;
        }

        div.general-wrap {
          @include bp-m {margin-top: ($vmargin*1.5);}
        }

        div.form-box {margin-bottom: 0;}
      }
    }

    &.contact {
      text-align: center;

      div.wrap {display: block;} /* required reset */

      div.col {

        h2 {@extend .lead;}

        label {@include bp-s-less {display: block;}}

        > p {
          @include bp-m {margin: 0;}
        }
      }
    }

    &.newsletters {
      div.compact-form {
        margin: 0;

        fieldset {
          @include bp-m {
            display: flex;
            align-items: center;
          }

          > p {
            @extend .h3;
            font-weight: 300;

            @include bp-m {
              flex: 0 1 auto;
              align-self: center;
              margin: 0;
              max-width: 60%; // IE 11 bug fix to make the container shrink on smaller screens
            }
          }

          div.form-group {
            margin: 0 auto;
            max-width: 520px;
            
            @include bp-m {
              flex: 1 1 auto;
              margin: 0 0 0 $spacing;
              min-width: 450px;
            }
          }
        }
      }
    }
  }
}

// page intro

div.page-intro {
  padding: ($spacing*4) 0 ($spacing*2);
  text-align: center;

  @include bp-m {padding: ($spacing*5.5) 0 ($spacing*2.5);}

  body.about & {
    @include bp-m-less {padding-bottom: 0;}
  }

  h1 {
    margin-bottom: 0;
    @include bp-l {font-size: $fs-70;}

    body.error & {
      @extend .h2;
      span {
        display: block;
        font-size: $fs-70;
        @include bp-m {font-size: $fs-100;}
      }
    }
  }

  p.lead {
    margin: $vmargin auto 0;
    @include bp-m {max-width: 53rem;}

    body.form-response & {
      display: inline-block;
      border-radius: $border-radius;
      padding: $spacing ($spacing*2);
      background: $clr-4;
    }

    span {@include bp-m {display: block;}}
  }

  p.cta {margin: $vmargin 0 0;}
}

// services intro

div.services-intro {
  @extend .page-intro;

  &:before {background-position: 0 20%;}

  h1 {

    span.hierarchy {
      display: block;
      line-height: 1rem;

      strong {
        display: inline-block;
        margin-bottom: 1rem;
        border-radius: 2rem;
        padding: 0.35rem 1rem;
        background: rgba(black,0.3);
        color: white;
        font-size: $fs-16;
        font-weight: 400;

        @include bp-m {font-size: $fs-18;}
      }
    }

    span {
      @include bp-m {display: block;}
    }
  }
}

// guide intro

div.guide-intro {
  @extend .page-intro;

  @include bp-m {padding-bottom: $spacing*3;}

  &:before {background-position: 0 20%;}

  h1,h2 {
    body:not([class*="index"]) & {@extend .h1;}
  }

  p.lead {margin-right: 0; margin-left: 0; max-width: none;}

  @include bp-s-less {p.cta label.button {display: block;}}
}

// home intro

div.home-intro {
  background: white;

  input#question-funnel-1:checked ~ div.intro-text {@extend .bg-b2;}
  input#question-funnel-2:checked ~ div.intro-text {@extend .bg-b4;}
  input#question-funnel-3:checked ~ div.intro-text {@extend .bg-b3;}

  div.intro-text {
    @extend .page-intro;
    position: relative;
    padding-bottom: 0;

    div.wrap {
      position: relative;
      z-index: 2;
    }

    h1 {
      position: relative;
      z-index: 10;
      margin-bottom: $vmargin;

      @include bp-s {margin-bottom: $vmargin*1.5;}
      @include bp-m {margin-bottom: $vmargin*2;}
      @include bp-l {margin-bottom: $vmargin*1.5;}

      span {display: block;}
    }

    p.question-intro {
      display: inline-block;
      margin: $vmargin 0;
      border-radius: 0.5rem;
      padding: 0.5rem 1rem;
      background: rgba(black,0.15);
      font-size: $fs-25;
      font-weight: 300;

      @include bp-m {
        margin: ($vmargin*4) 0 ($vmargin*2);
        font-size: $fs-30;
      }

    }
  }

  div.question-funnels-content {
    padding-bottom: $spacing*2;
  }
}

// article pages

body.article {
  article {
    figure {border-bottom: 1px solid white;}

    div.strip div.wrap {
      h1 {@extend .h2-ext;}
      > p:nth-of-type(1):not([class]) {@extend .lead;}
      > ul {@extend .disc;}
      > ol {@extend .decimal;}
    }
  }
}

// guide pages

div.guide-content {

  @include bp-m {
    position: relative;
    z-index: 2;
    margin: -1.5rem 0 0;
    padding: 0 0 $vmargin;
    background: transparent;
  }

  div.wrap > div {

    @include bp-m {
      border-radius: 0.25rem;
      padding: ($spacing*2);
      background: white;
      box-shadow: 0 0 4px 2px rgba(black,0.1);
    }

    > ul:not([class]) {@extend .disc;}
    > ol:not([class]) {@extend .decimal;}
    > :last-child {margin-bottom: 0;}

    @include bp-m-less {
      p.back-top {@include hide-screen();}
    }
  }
}

// general wrap for lists

div.general-wrap {
  overflow: hidden;
  margin-bottom: $vmargin;
  border-radius: $border-radius;

  &.spacer {
    ~ p {
      @include bp-m {margin-top: ($vmargin*2);}
    }
  }

  > ol,
  > ul {
    margin-bottom: 0;

    > li {
      margin-top: 4px;
      &:nth-child(1) {margin-top: 0;}

      &.col-4,
      &.col-6 {
        @include bp-m {&:nth-child(2) {margin-top: 0;}}
      }

      &.col-4 {
        @include bp-m {&:nth-child(3) {margin-top: 0;}}
      }
    }
  }
}

// announcement

div.announcement {
  padding: ($spacing/1.5) 0;
  background: $clr-2;
  color: white;

  @include bp-m {text-align: center;}

  div.wrap > :last-child {margin-bottom: 0;}

  p strong {
    display: block;
    @include bp-m {display: inline;}
  }

  a {
    font-weight: 700;
    color: white;
  }
}

// promotions

div.promotion {
  padding: ($spacing/1.5) 0;
  background: $clr-b1;
  color: white;

  @include bp-m {text-align: center;}

  div.wrap > :last-child {margin-bottom: 0;}

  p strong {
    display: block;
    @include bp-m {display: inline;}
  }

  a {color: white;}
}

// notices

p.notice {
  position: relative;
  border: 3px solid $clr-3;
  border-radius: $border-radius;
  padding: 1rem;
  background: white;

  &:before {
    position: absolute;
    left: 0.8rem; top: 1.05rem;
    font-size: $fs-24;
  }

  &.error,
  &.success,
  &.warning,
  &.caution,
  &.info {padding-left: 2.5rem;}

  &.error {
    border-color: $clr-error;
    background: white;

    &:before {
      @extend .icon-extension,.icon-exclamation-circle;
      color: $clr-error;
    }
  }

  &.success {
    border-color: $clr-success;

    &:before {
      @extend .icon-extension,.icon-check-circle;
      color: $clr-success;
    }
  }

  &.offer {
    border-color: $clr-b1;
    background: rgba($clr-b1,0.05);
    text-align: center;

    &:before {content: none;}

    div.buy-details & {
      border-color: white;
      background: rgba(white,0.1);

      span {
        display: block;
        font-size: $fs-18;
      }

      b {font-size: $fs-22;}
    }
  }

  &.warning {
    border-color: $clr-warning;

    &:before {
      @extend .icon-extension,.icon-exclamation-triangle;
    }
  }

  &.caution {
    &:before {
      @extend .icon-extension,.icon-exclamation-circle;
    }
  }

  &.info {
    &:before {
      @extend .icon-extension,.icon-info-circle;
    }
  }
}

// contact form inline

input#contact-form-inline {
  @include hide-screen();

  &:checked {
    + div.row {
      label.contact-form-inline-cta span[class*="icon-"]:before {@extend .icon-minus-circle;}
      + div.contact-form-inline {display: block;}
    }
  }
}

div.contact-form-inline {
  display: none;
  margin: $vmargin auto 0;
  border-radius: $border-radius;
  padding: $spacing;
  max-width: 30rem;
  background: $clr-1;

  form button.button {
    @include bp-s-less {width: 100%;}
  }
}

// features box 

div.features-box {
  overflow: hidden;
  margin: 0 0 $vmargin;
  border-radius: $border-radius;

  @include bp-m {margin-top: $vmargin*2;}

  h2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0;
    text-align: center;
    @include bp-m {text-align: right;}

    span {
      position: relative;
      z-index: 2;
      display: block;
      padding: ($spacing*1.5);
    }
  }

  div.content {
    padding: ($spacing*1.5) $spacing;
    background: $clr-4;

    ol {
      margin-bottom: 0;

      li {
        display: flex;
        margin-bottom: $vmargin;

        &:last-child {margin-bottom: 0;}

        &:before {
          flex: 0 1 auto;
          margin-right: 0.75rem;
          font-size: $fs-30;
          color: $clr-b2;
        }
      }
    }
  }
}

// guide navigation

input.guide-nav-toggle {
  position: fixed;
  top: 50%; left: -1000em;
  z-index: -1;
  visibility: hidden;

  &:checked + nav.guide-nav {

    @include bp-m {left: 0;}

    label.cta {
      span.button {
        box-shadow: inset 0 1px 2px rgba(0,0,0,0.75), 0 0 0 5px rgba(white,0.1);
        &:before {@extend .icon-times;}
      }

      &:after {@include bp-m {content: "";}}
    }

    div.guide-links {
      right: 0; left: 0;

      @include bp-m {
        right: auto; left: auto;
        box-shadow: 0.25rem 0 1.5rem rgba(black,0.3);
      }
    }

    + article div.guide-intro p.cta label span[class*="icon-"]:before,
    + article div.guide-content div.guide-nav-end label span[class*="icon-"]:before {@extend .icon-times;}
  }
}

nav.guide-nav {
  position: fixed;
  right: 0; bottom: 0;
  padding-bottom: 1rem;
  z-index: 1000;

  @include bp-m {
    right: auto; top: 0; left: -27rem;
    padding-bottom: 0; 
    width: 27rem;
    display: flex;
    align-items: stretch;
    flex-direction: column;
    transition: left 0.25s;
  }

  label.cta {
    cursor: pointer;
      
    span.button {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      z-index: 2;
      margin-right: 1rem;
      border-radius: 3rem;
      padding: 0!important;
      width: 2.5rem;
      height: 2.5rem;

      &:before {
        margin: 0!important;
        font-size: $fs-25;
      }

      @include bp-m {
        position: absolute;
        bottom: 0; left: 100%;
        margin: 0 0 1rem 1rem;
        width: 3rem;
        height: 3rem;

        &:before {font-size: $fs-30;}
      }

      span {@include hide-screen();}
    }

    &:after {
      position: fixed;
      left: 0; right: 0; bottom: 0; top: 0;
      z-index: 1;
      background: rgba(black,0.3);
    }
  }

  div.guide-links {
    position: fixed;
    left: -1000em; top: 0; bottom: 0;
    z-index: 1;
    background: $clr-1;
    text-align: center;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;

    @include bp-m {
      flex: 1;
      position: relative;
      left: auto; top: auto; bottom: auto;
      box-shadow: 0 0 0 rgba(black,0.3);
      transition: box-shadow 0.25s;
    }

    h2 {
      @extend .h4;
      margin: 0;
      padding: 0.75rem 1rem;
      background: rgba(black,0.3);
      color: white;
      font-weight: 300;

      @include bp-m {text-align: left;}
    }

    > ul {
      display: inline-block;
      margin-bottom: 0;
      padding: 0.75rem 0.5rem 2rem 1px;
      font-size: $fs-18;
      text-align: left;

      @include bp-m {
        display: block;
        font-size: $fs-20;
      }

      ul {
        margin-bottom: 0;

        li {
          margin-left: 1.3rem;
          border-left: 1px solid rgba(white,0.2);
        }
      }

      > li {

        > ul {
          padding-left: 0.5rem;
          > li {margin-left: 0.55rem;}
        }

        > a { 
          padding-left: 1rem;
          font-weight: 500;

          &:before {content: none;}
        }
      }

      a {
        display: flex;
        border-radius: 0 0.25rem 0.25rem 0;
        padding: 0.2rem 0.5rem;
        padding-left: 0;
        text-decoration: none;
        color: white;
        
        @include bp-m {
          &:hover,
          &:focus {background: rgba(white,0.075);}
        }

        &.current {
          background: rgba(black,0.3);
          box-shadow: -1px 0 0 $clr-b1, inset 4px 0 0 $clr-b1;
          &:before {color: $clr-b1;}
        }

        &:before {
          flex: 0 1 auto;
          color: rgba(white,0.2);
          content: "\02014\000A0";
        }

        span {flex: 1 1 auto;}
      }
    }
  }
}

// guide funnel

div.guide-funnel {
  overflow: hidden;
  position: relative;
  margin: 0 (-$gutter) $vmargin;
  background-color: $clr-b1;
  color: white;

  &.bg-b2 {background-color: $clr-b2;}
  &.bg-b3 {background-color: $clr-b3;}
  &.bg-b4 {background-color: $clr-b4;}

  @include bp-s {
    margin: 0 0 $vmargin;
    border-radius: $border-radius;
  }

  @include bp-m {
    float: right;
    margin: 0 0 $vmargin $spacing;
  }

  &.small {

    a {
      display: block;
      position: relative;
      z-index: 2;
      padding: $spacing 0.5rem;
      color: white;
      text-decoration: none;
      text-align: center;
      transition: background 0.2s;

      @include bp-m {
        padding: $spacing;
        text-align: left;
        font-size: $fs-24;

        &:hover,
        &:focus {background: rgba(black,0.1);}
      }

      b {
        display: block;
        margin-top: 0.25rem;
        font-weight: 500;

        @include bp-m {
          display: flex;
          align-items: center;
          font-size: $fs-27;
        }

        &:before {
          display: block;
          font-size: $fs-35;

          @include bp-m {margin-right: 0.25em;}
        }
      }
    }
  }

  &.medium {
    padding: $spacing 0.5rem;
    text-align: center;

    @include bp-m {
      padding: $spacing;
      max-width: 18rem;
    }

    div.content {
      position: relative;
      z-index: 2;

      h3 {
        margin-bottom: 0.75rem;
        b {display: block;}
      }

      p {
        margin-bottom: 1rem;

        a.button {
          font-size: $fs-20;
          @include bp-s-less {display: block;}
        }

        &.small-text a {color: white;}
      }

      > :last-child {margin-bottom: 0;}
    }
  }
}

// supporting info - on guide and article pages

div.supporting-info {
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
  margin: 0 0 $vmargin;
  border-bottom: 5px solid $clr-4;
  padding: 0.5rem 0;

  ul, p {margin: 0.25rem 0;}

  &.end {
    margin-bottom: 0;
    border-bottom: 0;
    border-top: 5px solid $clr-4;
    background: none;

    div.guide-nav-end + & {border-top-color: $clr-3;}

    ul.share-options {flex: 1 1 auto;}
  }

  ul.share-options {flex: 0 0 auto;}

  ul.general {
    flex: 1 0 auto;
    list-style: none;
    font-size: $fs-16;

    @include bp-m {display: flex;}

    li {
      display: flex;
      align-items: center;
      margin-right: 1rem;

      &:before {margin-right: 0.25rem;}

      a {
        text-decoration: none;
        font-weight: 600;
        color: $clr-1;

        @include bp-m {
          &:hover,
          &:focus {color: $clr-b1;}
        }
      }
    }
  }

  p.top {
    flex: 0 0 auto;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 2rem;
      width: 1.7rem;
      height: 1.7rem;
      background: $clr-b1;
      text-decoration: none;
      text-align: center;
      color: white;
      transition: box-shadow 0.1s;

      @include bp-m {
        &:hover,
        &:focus {
          box-shadow: inset 0 0 0 4rem rgba(black,0.1);
        }
      }

      span {@include hide-screen();}
    }
  }
}

// guide nav end

div.guide-nav-end {
  clear: both;
  border-radius: 0.25rem 0.25rem 0 0;
  padding: 0.75rem;
  background: $clr-4;
  text-align: center;

  @include bp-s-less {
    label.button {display: block;}
  }
}

// pdf embed

div.sample-preview {
  display: flex;
  align-items: stretch;
}

div.pdf-insert {
  flex: 1;
  position: relative;
  border-bottom: 5px solid #4F5356;
  background: #4F5356;
  box-shadow: 0 0 10px 5px rgba(black,0.1);

  @-moz-document url-prefix() {
    border-bottom: 20px solid #2A2A2E;
    background: #2A2A2E;
  }
  
  @include bp-m-less {display: none;}

  object {
    position: absolute;
    top: 0; left: 0;
    border: 0;
    width: 100%;
    height: 100%;
    background: rgba(white,0.3);
  }
}

p.pdf-legacy {
  padding: ($spacing*2);
  text-align: center;
}

// video insert

div.video-insert {
  position: relative;

  &.playing {
    &:before {
      display: block;
      padding-top: 56.23%;
      content: "";
    }
  }

  &.playing,
  a.cta {
    margin: 0 (-$gutter) $vmargin;
    background: black;

    @include bp-m {
      margin: 0 auto $vmargin;
      box-shadow: 0 0 10px 5px rgba(black,0.2);

      body.guide article & {box-shadow: none;}
    }

    div.specialities & {
      margin: ($vmargin*1.5) auto;
      @include bp-m {margin: ($vmargin*2) auto;}
    }

    div.strip.downloads & {
      margin-top: $vmargin;
      margin-bottom: $vmargin;

      @include bp-m {
        margin-top: ($vmargin*2);
        margin-bottom: ($vmargin*2);
      }
    }
  }

  a.cta {
    position: relative;
    display: block;
    transition: filter 0.2s;

    @include bp-m {
      max-width: 600px;

      body.guide article & {
        float: right;
        clear: both;
        margin: 0 0 0 $spacing;
        width: 50%;
      }

      &:hover,
      &:focus {filter: brightness(90%);}
    }

    &:before {
      position: relative;
      z-index: 2;
      display: block;
      padding-top: 56.23%;
      background: url(../../assets/images/content/other/play.png) no-repeat 50%;
      cursor: pointer;
      content: "";
    }

    img {
      position: absolute;
      right: 0; top: 0; left: 0; bottom: 0;
      z-index: 1;
      width: 100%;
      cursor: pointer;
    }
  }

  object,
  video,
  iframe {
    position: absolute;
    top: 0; left: 0;
    border: 0;
    width: 100%;
    height: 100%;
    background: transparent;
  }
}

body.guide article {
  p.video-caption {
    margin: 0 (-$gutter) $vmargin;
    padding: 0.5rem;
    background: $clr-4;
    font-size: $fs-16;

    @include bp-m {
      max-width: 600px;
      float: right;
      clear: both;
      margin: 0 0 $vmargin $spacing;
      border-radius: 0 0 0.25rem 0.25rem;
      width: 50%;
    }
  }

  div.video-insert {

    &.playing {
      margin-bottom: 0;

      + p.video-caption {
        max-width: none;
        float: none;
        margin: 0 (-$gutter) $vmargin;
        width: auto;

        @include bp-m {margin: 0 0 $vmargin;}
      }
    }

    a.cta {margin-bottom: 0;}
  }
}

// contact button

input.contact-button-toggle {
  position: fixed;
  top: 50%; left: -1000em;
  z-index: -1;
  visibility: hidden;

  &:checked + div.contact-button {

    label.cta {
      display: block;

      span.button {
        box-shadow: inset 0 1px 2px rgba(black,0.75), 0 0 0 5px rgba(white,0.1);
        &:before {@extend .icon-times;}
      }

      &:after {@include bp-s {content: "";}}
    }

    div.form {
      top: 0; right: 0;

      @include bp-s-less {left: 0;}

      @include bp-s {
        top: auto;
        margin-bottom: 5rem;
        opacity: 1;
        transition: margin-bottom 0.25s, opacity 0.25s;
      }
    }
  }
}

div.contact-button {
  position: fixed;
  bottom: 0; right: 0;
  z-index: 1000;
  padding-bottom: 1rem;

  label.cta {
    display: none;
    cursor: pointer;

    body.guide & {display: none;}

    @include bp-s {display: block;}

    span.button {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      z-index: 2;
      margin-right: 1rem;
      border-radius: 3rem;
      padding: 0!important;
      width: 2.5rem;
      height: 2.5rem;

      &:before {
        margin: 0!important;
        font-size: $fs-25;
      }

      @include bp-m {
        width: 3rem;
        height: 3rem;

        &:before {font-size: $fs-30;}
      }

      span {@include hide-screen();}
    }

    &:after {
      position: fixed;
      left: 0; right: 0; bottom: 0; top: 0;
      z-index: 1;
      background: rgba(black,0.3);
    }
  }

  div.form {
    position: fixed;
    bottom: 0; right: 1000em;
    z-index: 1;
    background: $clr-4;

    @include bp-s {
      margin: 0 1rem 4rem 0;
      border-radius: $border-radius;
      width: 16rem;
      background: none;
      opacity: 0;
      box-shadow: 0.25rem 0.25rem 1.5rem rgba(black,0.3);
      transition: margin-bottom 0.25s, opacity 0.25s, right 0s 0.25s;
    }

    h2 {
      @extend .h3;
      position: relative;
      margin: 0;
      background: $clr-2;
      color: white;
      padding: 0.5rem 1rem;

      @include bp-s {border-radius: $border-radius $border-radius 0 0;}

      &:after {
        position: absolute;
        right: 1rem;
        top: 0;
        border-radius: 3rem;
        width: 3rem;
        height: 3rem;
        background: white url(../../assets/images/content/other/contact-button-person.jpg) no-repeat 50%;
        background-size: cover;
        box-shadow: 0 0 0 5px $clr-2;
        content: "";

        @include bp-s {top: -0.15rem;}
      }
    }

    form {
      padding: 1rem;
      background: $clr-4;

      @include bp-s {border-radius: 0 0 $border-radius $border-radius;}

      div.form-group {
        margin: 0 0 ($vmargin/2);

        &.linear-fields {
          margin-bottom: 0;

          label {
            margin-top: 0;
            margin-bottom: ($vmargin/2);
          }
        }

        input:not([type=checkbox]):not([type=radio]):not([type=submit]):not([type=file]),
        textarea,
        .custom-select {
          padding: 0.4rem 0.5rem;
          font-size: $fs-18;
        }
      }

      button {
        width: 100%;
        font-size: $fs-20;
      }
    }
  }
}

// medical professional on about page

div.med-pro {
  position: relative;
  overflow: hidden;
  margin-bottom: $vmargin;
  border-radius: $border-radius;
  background: $clr-4;
  text-align: left;

  @include bp-m {text-align: right;}

  &:before {
    display: block;
    height: 15rem;
    background: $clr-3 url(../../assets/images/content/other/medical-professionals.jpg) no-repeat 50%;
    background-size: cover;
    content: "";

    @include bp-m {
      position: absolute;
      top: 0; left: 0; bottom: 0;
      border-right: 4px solid white;
      width: 50%;
      height: auto;
    }
  }

  p {
    @extend .lead;
    margin-bottom: 0;
    padding: 1rem;
    text-align: left;

    @include bp-s {padding: 2rem;}
    
    @include bp-m {
      display: inline-block;
      padding: 4rem 2rem;
      width: 50%;
    }
  }
}

// purchase info

div.purchase-info {
  overflow: hidden;
  margin: 0 (-$gutter) $vmargin;
  padding: $spacing;
  text-align: center;

  @include bp-s {
    margin: 0 0 $vmargin;
    border-radius: $border-radius;
  }

  p.prices {
    margin-bottom: 0.5rem;

    span {display: block;}
    b {@include hide-screen();}

    span.was {
      font-size: $fs-18;

      span.price span {
        display: inline-block;
        text-decoration: line-through;
      }
    }

    span.current {
      span.price {
        font-size: $fs-30;
        line-height: 1.1;
      }

      span.vat {
        display: block;
        font-size: $fs-14;
      }

      span {
        @include bp-m {display: inline-block;}
      }
    }
  }

  ul.buttons {
    margin: 0;

    li {
      display: block;
      margin-right: 0;

      a.button {display: block;}
    }
  }

  p.cta {
    margin: 0;
    a.button {display: block;}
  }

  p.information {
    margin: 1rem 0 0;
    font-size: $fs-16;
    span {
      @include bp-m {display: block;}
    }
  }
}

// additional info

div.additional-info {
  border-radius: $border-radius;
  padding: $spacing;
  background: $clr-4;
  font-size: $fs-16;

  div.strip.bg-b5 & {background: rgba($clr-4,0.4);}

  p.list-lead {
    margin-bottom: 0.2rem;
    font-weight: 500;
  }

  ul.checked {
    margin-bottom: 0;
    
    li {margin: 0;}
  }

  ul.share-options {margin-top: $vmargin;}
}

// testimonials

div.testimonials {
  margin: $vmargin 0;

  ul {
    margin-bottom: 0;
    text-align: center;

    li {

      input {
        position: fixed;
        top: 50%; left: -1000em;
        z-index: -1;
        visibility: hidden;

        &:checked {
          + div.content {
            overflow: visible;
            visibility: visible;
            margin-left: 0;
            padding: 1.25rem 0;
            width: 100%;
            max-height: 30rem;
            opacity: 1;
            transition: opacity 0.25s 0.25s, margin-left 0.25s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

            @include bp-m {padding: 3rem 0;}
          }
        }
      }

      div.content {
        display: flex;
        justify-content: center;
        overflow: hidden;
        visibility: hidden;
        margin-left: -20rem;
        padding: 0;
        width: 0;
        max-height: 0;
        opacity: 0;
        transition: opacity 0.25s, margin-left 0.25s, visibility 0s 0.25s, width 0s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

        blockquote {
          position: relative;
          display: flex;
          flex-direction: column;
          justify-content: center;
          margin-bottom: 0;
          border-left: 5px solid $clr-b1;
          border-radius: $border-radius;
          padding: $spacing ($spacing/1.7);
          max-width: 650px;
          min-height: 20rem;
          background: rgba(black,0.05);

          @include bp-s {
            padding: $spacing;
            min-height: 15rem;
          }

          @include bp-m {padding: $spacing ($spacing*1.5);}

          div.quote p {
            @extend .h4;

            &:before {content: "\0201C";}
            &:after {content: "\0201D";}
          }

          p.source {
            margin-bottom: 0;
            font-style: normal;
            @include bp-m-less {font-size: $fs-18;}
            span {display: block;}
          }
        }

        label.button {
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          right: 50%; top: 100%;
          z-index: 10;
          margin: -1.25rem -1.25rem 0 0;
          border-radius: 3rem;
          padding: 0!important;
          width: 2.5rem;
          height: 2.5rem;
          font-size: $fs-25;

          @include bp-m {
            right: 0; top: 50%;
            margin: -1.25rem -1.25rem 0 0;
          }

          &:before {
            @extend .icon-extension, .icon-angle-right;
          }

          span {@include hide-screen();}
        }
      }
    }
  }
}

// alternative contact - talk + support pages

div.alternative-contact {
  margin: 0 auto;
  text-align: center;

  @include bp-m {margin-top: ($vmargin*1.5);}

  h2 {
    @extend .h3;
    margin-bottom: ($vmargin/2);
  }

  p {
    margin-bottom: 0;
    @include bp-m {span {display: block;}}
  }
}

// question tool

$toolHeight: 80rem;
$toolAnimation: 0.2s;

div.question-tool {
  margin: $vmargin 0;
  text-align: center;

  @include bp-m {
    body.services & {margin: ($vmargin*2.5) 0 ($vmargin*1.5);}
  }

  input[type="radio"] {
    position: fixed;
    top: 50%; left: -1000em;
    z-index: -1;
    visibility: hidden;
  }

  div.question-tool-start {overflow: hidden;}
      
  div.question-content,
  div.answer-content {
    overflow: hidden;
    max-height: 0;
    opacity: 0;
  }

  input#question-tool-start:checked,
  input[id*="question-yes"]:checked + input,
  input[id*="question-no"]:checked {
    + div.question-tool-start,
    + div.question-content {
      max-height: 0!important;
      opacity: 0!important;
      transition: opacity $toolAnimation ($toolAnimation*2), max-height 0s ($toolAnimation*4)!important;

      + input + input + div[class*="question-content"],
      + div.answer-content {
        max-height: $toolHeight;
        opacity: 1;
        transition: opacity $toolAnimation ($toolAnimation*4), max-height 0s ($toolAnimation*4); 
      }
    }
  }

  input[id*="last-question-input"]:checked ~ div.question-content + div.answer-content {
    max-height: $toolHeight;
    opacity: 1;
  }

  input[id*="question-yes"]:checked ~ div.answer-content {
    .answer-content-yes {display: block;}
    .answer-content-no {display: none;}
    h3.answer-content-yes {
      display: block;
      @include bp-m {display: flex;}
    }
  }

  // styling

  input[id*="question-yes"]:checked + input + div.question-content ul li label[for*="question-yes"],
  input[id*="question-no"]:checked + div.question-content ul li label[for*="question-no"] {
    background: $clr-b1;
    color: white;
    span.radio {border-color: $clr-b1;}
    span.radio:before {background: $clr-2; content: "";} // fill radio button
  }

  // question tool start

  div.question-tool-start {
    margin: 0;
    max-height: $toolHeight;

    @include bp-m {
      p.lead span {display: block;}
    }

    h2 {
      @extend .bg-b3;
      margin-bottom: 0;
      border-radius: $border-radius $border-radius 0 0;
      padding: 1rem;
      color: white;
    }

    div.text {
      border-radius: 0 0 $border-radius $border-radius;
      padding: 1rem;
      background: $clr-4;
      text-align: center;

      div.strip.bg-b5 & {background: rgba($clr-2,0.075);}

      @include bp-m {
        padding: 2rem;
      }

      > :last-child {margin-bottom: 0;}
    }
  }

  // question content

  div.question-content {

    h3 {
      @extend .h2;
      overflow: hidden;
      position: relative;
      margin-bottom: 4px;

      span.number { 
        @extend .bg-b3;
        display: block;
        border-radius: $border-radius $border-radius 0 0;
        padding: 0.75rem;
        color: white;

        @include bp-m {padding: 1rem;}
      }

      span.text {
        display: flex;
        justify-content: center;
        flex-direction: column;
        padding: 1rem;
        min-height: 12.5rem;
        background: $clr-4;
        font-size: $fs-20;

        @include bp-s {
          padding: 1rem 2rem;
          min-height: 8.5rem;
        }

        @include bp-m {
          min-height: 7.5rem;
          font-size: $fs-25;
        }

        div.strip.bg-b5 & {background: rgba($clr-2,0.075);}
      }
    }

    ul {
      display: flex;
      margin: 0;

      li {
        flex: 1;
        border-top-width: 4px;
        width: 50%;

        label {
          display: flex;
          justify-content: center;
          padding: 1.05rem; // unusal padding match height of div.question-tool-start
          background: $clr-4;
          font-size: $fs-22;
          color: $clr-b0;
          cursor: pointer;
          user-select: none;
          -webkit-user-select: none;
          
          div.strip.bg-b5 & {background: rgba($clr-2,0.125);}

          @include bp-m {
            font-size: $fs-25;

            &:hover {
              background: $clr-3;
              div[class*="bg-b"] & {background: rgba(white,0.7);}
              span.radio {border-color: $clr-b0;}
            }
          }

          span {
            line-height: 1;
            &.radio {border-width: 2px;}
          }
        }

        &:first-child label {
          margin-right: 2px;
          border-radius: 0 0 0 $border-radius;
        }

        &:last-child label {
          margin-left: 2px;
          border-radius: 0 0 $border-radius 0;
        }
      }
    }
  }

  // answer content

  div.answer-content {

    .answer-content-yes {display: none;} // default hiding

    h3 {
      @extend .bg-b5;
      margin-bottom: 0;
      border-radius: $border-radius $border-radius 0 0;
      padding: 1rem;
      background-color: $clr-success;
      color: white;

      @include bp-m {
        display: flex;
        justify-content: center;
        text-align: left;
        @-moz-document url-prefix() {align-items: center;}
      }

      &.answer-content-yes {
        background-color: $clr-warning;
        color: $clr-1;

        &:before {
          @extend .icon-exclamation-circle;
        }
      }

      &:before {
        @extend .icon-extension, .icon-check-circle;
        font-size: $fs-40;
        @include bp-m {margin-right: 0.5rem;}
      }

      span {
        display: block;
        strong {
          text-decoration: underline;
          text-decoration-thickness: 4px;
          text-underline-offset: 3px;

          @include bp-s-less {white-space: nowrap;}
        }
      }
    }

    div.text {
      border-radius: 0 0 $border-radius $border-radius;
      padding: 1rem;
      background: $clr-4;
      text-align: left;

      div.strip.bg-b5 & {background: rgba($clr-2,0.075);}

      @include bp-m {
        padding: 2rem;
        div.col > div > :last-child {margin-bottom: 0;}

        div.answer-content-yes p {
          font-size: $fs-22;
          font-weight: 300;
        }
      }

      div.form-group {margin-top: 0;}
      button {width: 100%;}
    }
  }
}

// question funnels

div.question-funnels {
  
  input[type="radio"] {
    position: fixed;
    top: 50%; left: -1000em;
    z-index: -1;
    visibility: hidden;
  }

  input#question-funnel-1:checked ~ div.intro-text div.question-funnels-options ul li label[for="question-funnel-1"],
  input#question-funnel-2:checked ~ div.intro-text div.question-funnels-options ul li label[for="question-funnel-2"],
  input#question-funnel-3:checked ~ div.intro-text div.question-funnels-options ul li label[for="question-funnel-3"] {
    background: white;
    color: $clr-1;

    &:before {
      position: absolute;
      top: 1rem; left: 1rem;
      border-radius: 3rem;
      width: 1.1rem; height: 1.1rem;
      background: $clr-4;
      box-shadow: 0 0 0 2px rgba(black,0.15);
      content: "";
    }

    &:after {content: "";} 
  }

  input#question-funnel-1:checked ~ div.intro-text ~ div.question-funnels-content div.question-funnel-1,
  input#question-funnel-2:checked ~ div.intro-text ~ div.question-funnels-content div.question-funnel-2,
  input#question-funnel-3:checked ~ div.intro-text ~ div.question-funnels-content div.question-funnel-3 {
    display: block;
  }

  // intro text

  div.intro-text {background: $clr-4;}

  // options

  div.question-funnels-options {
    overflow-x: scroll;
    margin: 0 (-$gutter);
    text-align: left;

    @include bp-m {margin: 0 (-$gutter*2);}

    @include bp-l {
      margin: 0;
    }

    ul {
      display: flex;
      margin: 0;
      min-width: 768px;
      
      li {
        display: flex;
        align-items: stretch;
        flex: 1;

        label {
          position: relative;
          display: block;
          margin: 0 2px;
          padding: ($spacing*2.5) $spacing $spacing;
          background: rgba(black,0.15);
          font-size: $fs-22;
          font-weight: 300;
          cursor: pointer;

          @include bp-m {
            padding-bottom: ($spacing*2);
            font-size: $fs-25;

            &:hover,
            &:focus {background: rgba(black,0.25);}
          }

          @include bp-l {border-radius: $border-radius $border-radius 0 0;}

          &:before {
            position: absolute;
            top: 1rem; left: 1rem;
            border-radius: 3rem;
            width: 1.1rem; height: 1.1rem;
            background: rgba(white,0.15);
            box-shadow: 0 0 0 2px rgba(white,0.3);
            content: "";
          }

          &:after {
            position: absolute;
            top: 1.2rem; left: 1.2rem;
            border-radius: 50%;
            width: 0.7rem;
            height: 0.7rem;
            background: $clr-b1;
          } 
        }

        &:first-child label {margin-left: 0;}
        &:last-child label {margin-right: 0;}
      }
    }
  }

  // content

  div.question-funnels-content {
    background: white;
    text-align: left;

    div.question-funnel {
      display: none;
      padding: ($spacing*2.5) 0 0;

      h2 {@extend .h2-ext;}
    }
  }
}
