// spaced list

.spaced {
  li {margin: 0.5rem 0;}
}

// disc list

.disc {
  margin-left: 1.25rem;
  list-style: disc;

  ul {
    margin-left: 1.25rem;
    list-style: circle;
  }
}

// decimal list

.decimal {
  margin-left: 1.25rem;
  list-style: decimal;
}

// lower alpha list

.lower-alpha li {
  margin-left: 1.25rem;
  list-style: lower-alpha;

  ol {margin-bottom: 0;}
}

// defintion list

dt {
  @extend .h4;
  font-weight: 500;
}

// checked list

ul.checked {
  margin-left: 0!important;
  list-style: none!important;

  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-check-circle;
      position: absolute;
      left: 0; top: 0.2rem;

      body.services & {color: $clr-b2;}
      body.academy &,
      ol.events & {color: $clr-b4;}
      body.resources & {color: $clr-b3;}
      div.about-success & {color: $clr-2;}
    }

    blockquote,
    blockquote p {margin-bottom: 0;}
  }

  &.plain a {
    text-decoration: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration: underline;}
    }
  }
}

// asterisk list

ul.asterisk {
  margin-left: 0!important;
  list-style: none!important;

  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-asterisk;
      position: absolute;
      left: 0; top: 0.2rem;

      body.services & {color: $clr-b2;}
      body.academy &,
      ol.events & {color: $clr-b4;}
      body.resources & {color: $clr-b3;}
      div.about-success & {color: $clr-2;}
    }

    blockquote,
    blockquote p {margin-bottom: 0;}
  }

  &.plain a {
    text-decoration: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration: underline;}
    }
  }
}

// chevroned list

ul.chevroned {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.5rem;

    &:before {
      @extend .icon-extension,.icon-angle-right;
      position: absolute;
      left: 0; top: 0;
      line-height: 1.4rem;
      font-size: $fs-16;

      body.services & {color: $clr-b2;}
      body.academy & {color: $clr-b4;}
      body.resources & {color: $clr-b3;}
    }
  }

  &.plain a {
    text-decoration: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration: underline;}
    }
  }
}

// buttons

ul.buttons {

  div.service-process div.text & {margin-bottom: 0;}

  li {
    display: inline-block;
    margin-bottom: 0.5rem;
    margin-right: 0.2rem;

    @include bp-s-less {display: block;}

    .button {
      @include bp-s-less {display: block;}
    }
  }
}

// questions

ul.questions {
  @extend .lead;

  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-asterisk;
      position: absolute;
      left: 0; top: 0.2rem;
    }
  }
}

// hierarchy links

div.hierarchy {
  padding: 0.4rem 0;
  background: $clr-3;

  ol {
    margin: 0;
    font-size: $fs-16;

    li {
      display: inline-block;

      &:before {
        @extend .icon-extension, .icon-angle-left;
        margin-right: 0.25rem;
        font-size: $fs-10;
        vertical-align: middle;
      }
    
      a, span {
        text-decoration: none;
        vertical-align: middle;
        color: #3B3D41;
      }
      
      a {
        @include bp-m {
          &:hover,
          &:focus {
            text-decoration: underline;
            color: $clr-b1;
          }
        }
      }
    }
  }
}

// brand assets

ul.brand-assets {
  border-top: 1px solid $clr-3;

  li {
    border-bottom: 1px solid $clr-3;
    padding: 0.4rem 0;

    a {
      text-decoration: none;
      @include bp-m {
        &:hover,
        &:focus {text-decoration: underline;}
      }
    }
  }
}

// document list

ul.documents {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.5rem;

    &:before {
      @extend .icon-extension,.icon-file;
      position: absolute;
      left: 0; top: 1px;
      font-size: $fs-24;
    }
  }
}

// numbered discs

ol.numbered-discs {
  margin-left: 0!important;
  list-style: none!important;
  counter-reset: numbered-discs;

  li {
    position: relative;
    margin-bottom: 0.5rem;
    padding-left: 2rem;

    &:before {
      position: absolute;
      left: 0; top: 0;
      margin-right: 1rem;
      border-radius: 50%;
      padding: 0;
      width: 1.3rem;
      height: 1.3rem;
      line-height: 1.4rem;
      background: $clr-b1;
      text-align: center;
      font-weight: 700;
      font-size: $fs-16;
      color: white;
      counter-increment: numbered-discs;
      content: counter(numbered-discs);

      body.services & {background: $clr-b2;}
      body.resources & {background: $clr-b3;}
      body.academy & {background: $clr-b4;}
    }
  }
}

// numbered blocks

ol.numbered-blocks {
  counter-reset: numbered-blocks;
  margin-top: 2rem;
  text-align: center;

  @include bp-m {
    margin-top: 0;
    text-align: left;
  }

  &.alt.alt li:before {
    @extend .icon-extension,.icon-check;
    background: $clr-b1;
  }

  li {
    position: relative;
    margin-bottom: 4px;
    padding: ($spacing*1.25) $spacing;
    background: $clr-4;

    &:first-child {border-radius: $border-radius $border-radius 0 0;}
    &:last-child {border-radius: 0 0 $border-radius $border-radius;}

    @include bp-m {padding-left: $spacing*1.75;}

    &:before {
      position: absolute;
      right: 50%; top: 0;
      margin: -1.3rem -1.25rem 0 0;
      border-radius: 50%;
      border: 4px solid white;
      padding-top: 0.35rem;
      width: 2.5rem;
      height: 2.5rem;
      background: $clr-b1;
      text-align: center;
      font-weight: 400;
      font-size: $fs-20;
      color: white;
      counter-increment: numbered-blocks;
      content: counter(numbered-blocks);
      box-sizing: border-box;

      @-moz-document url-prefix() {padding-top: 0.4rem;}

      body.services & {background: $clr-b2;}
      body.academy & {background: $clr-b4;}
      body.resources & {background: $clr-b3;}

      @include bp-m {
        right: 100%; top: 50%;
      }
    }

    > :last-child {margin-bottom: 0;}
  }
}

// information

ul.information {
  text-align: left;

  &.compact {
    div.content p {
      padding: ($spacing*0.75);
      font-size: $fs-20;

      @include bp-m {padding: $spacing;}
      &[class*="icon-"]:before {font-size: $fs-27;}
    }
  }

  div.content {
    flex: 1;
    background: $clr-4;

    h3 {
      @extend .h2;
      margin: 0;
      padding: ($spacing*0.75) $spacing;
      background: $clr-3;
      text-align: center;
    }

    p {
      margin: 0;
      padding: $spacing;
      font-size: $fs-22;

      @include bp-m {padding: $spacing*1.5;}

      em {
        display: block;
        margin-bottom: $vmargin/2;
        font: italic $fs-30 Georgia, Serif;
      }

      &[class*="icon-"] {
        display: flex;
        align-items: flex-start;

        &:before {
          flex: 0 1 auto;
          margin-right: 0.3em;
          font-size: $fs-30;
        }
      }

      &.icon-check-circle:before {
        color: $clr-success;

        .bg-dark & {color: white;}
      }
    }

    .bg-b5 & {
      background: rgba($clr-2,0.05);
      h3 {background: rgba($clr-2,0.05);}
    }

    .bg-dark & {
      background: rgba(black,0.2);
      h3 {background: rgba(black,0.5);}
    }
  }
}

// features grid

ul.features-grid {
  margin: 0;

  li.col {
    @extend .col-6;
    border: solid rgba($clr-2,0.075);
    border-width: 0 0 4px;
    padding: 0;

    &:last-child {border-bottom-width: 0;}

    @include bp-m {
      border-left-width: 2px;
      border-right-width: 2px;
      &:nth-child(odd) {border-left-width: 0;}
      &:nth-child(even) {border-right-width: 0;}
      &:nth-last-child(2) {border-bottom-width: 0;}
      &:nth-last-child(1) {border-bottom-width: 0;}
    }

    div.content {
      padding: ($spacing) 0;

      @include bp-m {
        flex: 1;
        padding: ($spacing*2);
      }

      &[class*="icon-"] {
        text-align: center;

        &:before {
          display: block;
          margin-bottom: 1rem;
          font-size: $fs-40;
          color: $clr-b1;

          @include bp-m {font-size: $fs-50;}

          body.services & {color: $clr-b2;}
          body.academy & {color: $clr-b4;}
          body.resources & {color: $clr-b3;}
        }
      }

      h3 {
        @extend .h2;
        margin-bottom: 1rem;
        span {display: block;}
      }

      > :last-child {margin-bottom: 0;}
    }
  }
}

// contact options

ul.contact-options {
  li {
    position: relative;
    padding-left: 1.5rem;

    &:before {
      position: absolute;
      top: 0.2rem; left: 0;
      font-size: $fs-20;

      footer.footer & {top: 0.05rem;}
    }

    &.email {
      overflow-wrap: break-word;
      word-wrap: break-word;

      span {
        @include bp-s-less {@include hide-screen();}
        footer & {@include hide-screen();}
      }
    }

    &.address {
      div.box &,
      footer & {@include hide-screen();}
      span {display: block;}
    }

    a {
      text-decoration: none;
      
      main & {
        @include bp-m {
          &:hover,
          &:focus {text-decoration: underline;}
        }
      }
    }

    span.icon-angle-right:before {
      display: inline-block;
      vertical-align: middle;
      margin: 0 0.5rem;
      font-size: $fs-10;
    }
  }
}

// call options

div.call-options.general-wrap {

  @include bp-s-less {
    margin-left: -$gutter;
    margin-right: -$gutter;
    border-radius: 0;
  }

  > ul {
    text-align: center;

    input.call-option {
      position: fixed;
      top: 50%; left: -1000em;
      z-index: -1;
      visibility: hidden;

      &:checked {
        ~ label {display: none;}
        ~ div.content {display: flex;}
      }
    }

    > li {
      @include bp-m {min-height: 23rem;}

      label.cta {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: ($spacing*2) $spacing;
        background: $clr-4;
        font-size: $fs-24;
        cursor: pointer;
        transition: background-color 0.15s;

        @include bp-m {font-size: $fs-30;}

        &:before {
          display: block;
          margin-bottom: 1rem;
          font-size: $fs-40;
          color: $clr-b1;

          @include bp-m {font-size: $fs-80;}
        }

        @include bp-m {
          &:hover,
          &:focus {background: $clr-3;}
        }

        b {
          text-decoration: underline;
          text-decoration-thickness: 4px;
          text-underline-offset: 3px;
          text-decoration-color: $clr-b1;
        }
      }

      div.content {
        display: none;
        flex: 1;
        flex-direction: column;
        justify-content: center;
        padding: $spacing;
        background: $clr-1;
        color: white;

        > :last-child {margin-bottom: 0;}

        @include bp-m-less {
          &.call-option-1 {padding: ($spacing*2) $spacing;}
        }

        h2 {
          @extend .h3;

          b {
            text-decoration: underline;
            text-decoration-thickness: 4px;
            text-underline-offset: 3px;
            text-decoration-color: $clr-b1;
          }
        }

        a {
          color: white;

          @include bp-m {
            &:hover,
            &:focus {text-decoration: none;}
          }
        }

        p.availability {
          font-size: $fs-16;
          span {display: block;}
        }

        p.email {
          font-size: $fs-18;
          span {display: block;}
        }

        p.timezone {
          margin: 0.5rem 0 0;
          font-size: $fs-16;
        }

        div.call-time {
          ol {
            overflow: hidden;
            display: flex;
            margin: 0;
            font-size: $fs-18;

            &.call-hours {border-radius: 0.25rem 0.25rem 0 0;}

            &.call-minutes {
              margin-top: 1px;
              border-radius: 0 0 0.25rem 0.25rem;
            }

            li {
              flex: 1;
              margin-right: 1px;
              background: $clr-2;


              &:last-child {margin-right: 0;}

              input {
                position: fixed;
                top: 50%; left: -1000em;
                z-index: -1;
                visibility: hidden;

                &:checked {
                  ~ label {
                    background: $clr-b1;
                    color: white;
                  }
                }
              }

              label {
                display: block;
                padding: 0.5rem 0.2rem;
                background: rgba(white,0.001);
                cursor: pointer;
                transition: background-color 0.15s;

                @include bp-m {
                  &:hover,
                  &:focus {
                    background: rgba(white,0.05);
                    color: $clr-b1;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

// site map

ul.site-map {
  overflow: hidden;
  border-radius: $border-radius;

  > li {

    a {
      display: block;
      border-bottom: 1px solid white;
      padding: 0.5rem 1rem;
      background: $clr-3;
      text-decoration: none;
      color: $clr-1;

      @include bp-m {
        &:hover,
        &:focus {
          color: $clr-b1;
          box-shadow: inset 0 0 0 100rem rgba($clr-2,0.05);
        }
      }
    }

    ul {
      margin: 0;
      font-size: $fs-18;
      
      li {
        a {
          padding-left: 2rem;
          background: $clr-4;
        }

        &:last-child a {border-bottom: 0;}
      }
    }
  }
}

// faqs 

ol.faqs {
  margin: 0;
  counter-reset: faq-item;

  li {
    margin: 0 0 4px;
    counter-increment: faq-item;

    &:last-child {margin-bottom: 0;}

    input {
      @include hide-screen();

      + h3 {
        @extend .h4;
        margin: 0;
        color: $clr-b0; // important for when in a dark background strip
          
        label {
          position: relative;
          display: block;
          margin: 0;
          padding: $spacing;
          padding-right: 3rem;
          background: $clr-4;
          font-weight: 400;
          color: $clr-1;
          cursor: pointer;
          user-select: none;
          -webkit-user-select: none;

          .bg-b5 & {background: rgba($clr-2,0.075);}

          &:before {
            @extend .icon-extension,.icon-plus-circle;
            position: absolute;
            top: 50%; right: 0;
            z-index: 1;
            margin: -0.5rem 1rem 0 0;
            font-size: 1em;
            color: $clr-b1;
          }

          @include bp-m {
            &:hover {
              background: $clr-3;
              color: $clr-b0;

              .bg-b5 & {background: rgba($clr-2,0.125);}
            }
          }

          span:before {
            content: counter(faq-item) ". ";
          }
        }

        + div.answer {
          position: absolute;
          left: -1000em;
          padding: $spacing;
          background: $clr-b5;
          color: $clr-1;
          -webkit-text-size-adjust: 100%; // forcing iOS to stop increasing text size

          div.section.bg-b5 & {background: white;}

          :last-child {margin-bottom: 0;}
        }
      }

      &:checked {
        + h3 {

          label {
            background: $clr-2!important; // prevent hover effect
            color: white;

            &:before {
              @extend .icon-minus-circle;
              color: white;
            }
          }

          + div.answer {position: static;}
        }
      }
    }
  }
}

// main funnels

ul.main-funnels {
  > li {
    &:nth-child(even) {
      > div.content {
        color: white;

        ul {
          background: rgba(black,0.2);
          li:before {color: white;}
        }
      }
    }

    > div.content {
      padding: ($spacing);
      text-align: center;

      @include bp-m {
        padding: ($spacing*2);
        text-align: left;
      }

      h2 {@extend .h2-ext;}

      ul {
        margin-bottom: 0;
        border-radius: $border-radius;
        padding: ($spacing*0.5) $spacing;
        background: rgba($clr-2,0.05);

        @include bp-m {font-size: $fs-22;}

        li {margin: 0.5rem 0;}
      }

      @include bp-m {
        div.row > div:first-child {order: 2;}
      }

      div.col > :last-child {
        @include bp-m {margin-bottom: 0;}
      }
    }
  }
}

// quick funnels

ul.quick-funnels {
  li {
    a.content {
      position: relative;
      display: block;
      padding: ($spacing/1.5) $spacing;
      padding-right: ($spacing*2.5);
      background: $clr-4;
      text-align: left;
      text-decoration: none;
      transition: background-color 0.15s;

      .bg-dark & {
        background: rgba(black,0.15);
        color: white;

        @include bp-m {
          &:hover,
          &:focus {background: rgba(black,0.25);}
        }

        &:before,
        p {color: white;}
      }

      @include bp-m {
        &:hover,
        &:focus {background: $clr-3;}
      }

      &:before {
        @extend .icon-extension, .icon-angle-right;
        position: absolute;
        right: 0; top: 50%;
        margin: -0.75rem 1rem 0 0;
        font-size: $fs-35;
        color: $clr-b1;
      }

      h3 {margin-bottom: $vmargin/3;}

      p {
        margin-bottom: 0;
        color: $clr-1;
      }
    }
  }
}

// contact buttons

ul.contact-buttons {
  li {
    min-width: 0; // hack to make the email address wrap inside a flex child 

    a.content {
      display: flex;
      align-items: center;
      padding: ($spacing/1.25) $spacing;
      min-width: 0; // hack to make the email address wrap inside a flex child 
      background: $clr-4;
      text-align: left;
      text-decoration: none;
      transition: background-color 0.15s;

      div.bg-b5 & {background: rgba($clr-2,0.075);}

      div.cta & {
        background: rgba(white,0.05);
        color: white;
      }

      @include bp-m {
        &:hover,
        &:focus {
          background: $clr-3;
          div.bg-b5 & {background: rgba($clr-2,0.125);}
          div.bg-dark & {background: rgba(white,0.075);}
        }
      }

      &:before {
        margin: 0 0.5rem 0 0;
        font-size: $fs-35;
        color: $clr-b1;
      }

      > span {
        @extend .text-wrap;
        min-width: 0; // hack to make the email address wrap inside a flex child 
        
        span.text {
          color: $clr-1;
          div.strip.cta & {color: white;}
        }
      }
    }
  }
}

// elements - general lists with/out links and icons

.elements.elements {
  overflow: hidden;
  margin: 0;

  &.two {
    li.col {
      @include bp-m {
        &:nth-child(odd) {padding-right: 2px;}
        &:nth-last-child(2) {padding-bottom: 0;}
        &:nth-child(even) {padding-left: 2px; padding-bottom: 4px;}
        &:nth-last-child(1) .content {border-radius: 0 0 $border-radius 0;}
      }
    }
  }

  li.col {
    margin: 0;
    padding: 0 0 4px;

    @include bp-m {
      display: flex;
      flex: 0 1 auto;
    }

    &:last-child {padding-bottom: 0!important;}

    .content {
      position: relative;
      display: block;
      padding: ($spacing) 0;
      background: white;
      text-decoration: none;
      transition: background-color 0.15s;

      @include bp-m {
        flex: 1;
        padding: ($spacing*2);
      }

      h2 {
        margin-bottom: 1rem;
        span {display: block;}
      }

      p,ul {color: $clr-1;}

      > :last-child {margin-bottom: 0;}
    }

    a.content {
      padding: $spacing;
      padding-bottom: $spacing*2;
      background: $clr-4;

      &:after {
        position: absolute;
        bottom: 0; left: 50%;
        display: inline-block;
        margin: 0 0 0 -4.25rem;
        border-radius: 0.25rem 0.25rem 0 0;
        padding: 0.2rem 0 0.1rem;
        width: 8.5rem;
        background: white;
        text-align: center;
        font-size: $fs-18;
        color: $clr-b1;
        content: "Learn more";
        transition: opacity 0.2s;
      }

      @include bp-m {
        padding: $spacing*1.4;
        padding-bottom: $spacing*1.75;

        &:after {opacity: 0;}

        &:hover,
        &:focus {
          background: $clr-3;

          &:after {opacity: 1;}
        }
      }
    }
  }

  &.downloads {
    > li {
      a.content h2 {
        @extend .h3;
        margin-bottom: $vmargin/2;

        span.info {
          display: flex;
          margin-bottom: 0.5rem;
          font-weight: 500;
          font-size: $fs-16;
          color: $clr-1;

          > span {
            flex: 0 1 auto;
            padding: 0.15rem 0.5rem;

            &:first-child {
              border-top-left-radius: 0.2rem;
              border-bottom-left-radius: 0.2rem;
            }

            &:last-child {
              border-top-right-radius: 0.2rem;
              border-bottom-right-radius: 0.2rem;
            }

            &.value {
              background: $clr-2;
              color: white;
              
              &.premium {background: $clr-b3;}
            }

            &.asset {background: rgba($clr-2,0.1);}
            &.file {background: white;}
          }
        }

        span.heading {display: block;}
      }

      &.free a.content:after {
        margin-left: -4rem;
        width: 8rem;
        content: "Free download";
      }
    }
  }
}

// event details

ul.event-details {
  overflow: hidden;
  border-radius: $border-radius;
  padding: 1rem;
  background: $clr-4;
  font-size: $fs-18;
  color: $clr-1;

  @include bp-s {padding: 0;}
 
  li {
    @include bp-s {display: flex;}

    b {
      font-weight: 500;

      @include bp-s {
        display: inline-block;
        flex: 0 0 auto;
        padding: 0.5rem;
        min-width: 9rem;
        background: $clr-3;
        text-align: right;
      }
    }

    span {

      @include bp-s {
        display: inline-block;
        flex: 1;
        padding: 0.5rem;
        box-shadow: inset 0 -1px rgba(black,0.05);
      }

      small {font-size: $fs-16;}
    }

    &:last-child > * {box-shadow: none;}
  }
}

// team

ul.team {
  position: relative;
  text-align: center;

  > li {
    position: relative;
    @include bp-m {position: static;}

    div.content {
      display: flex;
      flex: 1;

      h4 {
        @extend .h2;
        position: relative;
        display: flex;
        flex: 1;
        margin: 0;

        label {
          position: relative;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          flex: 1;
          padding: ($spacing*1.5) $spacing ($spacing*2.5);
          background: $clr-4;
          cursor: pointer;
          transition: background-color 0.2s, color 0.2s;

          &:after {
            position: absolute;
            bottom: 0; left: 50%;
            display: inline-block;
            margin: 0 0 0 -3.5rem;
            border-radius: 0.25rem 0.25rem 0 0;
            padding: 0.2rem 0 0.1rem;
            width: 7rem;
            background: white;
            text-align: center;
            font-size: $fs-18;
            font-weight: 400;
            line-height: $lh-general;
            color: $clr-b1;
            content: "View profile";
            transition: opacity 0.2s;
          }

          @include bp-m {
            padding: ($spacing*2.25) $spacing;

            &:after {opacity: 0;}

            &:hover,
            &:focus {
              background: $clr-3;
              &:after {opacity: 1;}
            }
          }

          @include bp-l {
            padding: ($spacing*1.75) $spacing;
          }

          span.image {
            display: block;
            position: relative;
            overflow: hidden;
            margin: 0 auto $vmargin;
            border-radius: 50%;
            width: 200px;

            img {
              position: absolute;
              top: 0; left: 0;
              border-radius: 50%;
              width: 100%;
            }

            &:before {
              display: block;
              padding-top: 100%;
              content: "";
            }
          }

          span {
            &.name {
              font-weight: 300;  
              color: $clr-b1;
            }

            &.title {
              display: block;
              margin-top: 0.25rem;
              font-weight: 400;
              font-size: $fs-18;
            }
          }
        }

        a.linkedin.linkedin {
          display: none;
          margin-top: 0.75rem;
          padding-left: 0.6rem;
          padding-right: 0.6rem;

          @include bp-m {
            align-items: center;
            justify-content: center;
            position: absolute;
            top: 0; left: 50%;
            margin: 75px 0 0 125px;
          }

          &:before {
            margin: 0;
            font-size: $fs-20;
            color: white;
          }

          span {@include hide-screen();}
        }
      }

      div.text {
        position: absolute;
        left: -1000em;
        align-items: flex-start;
        text-align: left;
        color: white;

        a.button span {@include hide-screen();}

        > div:last-child p {margin-bottom: 0;}
      }
    }

    input {
      @include hide-screen();

      &:checked + div.content {
        @extend .bg-b1;
        display: block; // reset flex
        padding: ($spacing*1.5) $spacing;
        background-color: $clr-1!important;

        @include bp-m {
          position: absolute;
          top: 0; left: 0; right: 0; bottom: 0;
          z-index: 1; // required
          padding: $spacing*2;
        }

        h4,
        div.text {
          position: relative;
          z-index: 2;
          left: auto;
          > :last-child {margin-bottom: 0;}
        }

        h4 {
          display: block; // reset flex
          margin-bottom: ($vmargin/2);
          color: white;

          @include bp-m {margin-bottom: $vmargin;}

          label {
            position: static;
            display: block;
            padding: 0;
            min-height: 0;
            background: none;
            cursor: default;
            transition: none;

            @include bp-m {
              display: inline-block;

              &:hover,
              &:focus {
                span.name {text-decoration: none;}
              }
            }

            &:before {
              @extend .icon-extension,.icon-times;
              display: flex;
              align-items: center;
              justify-content: center;
              position: absolute;
              right: -0.5rem; top: -1rem;
              border-radius: 50%;
              width: 1.5rem;
              height: 1.5rem;
              background: white;
              text-align: center;
              font-size: $fs-20;
              color: $clr-2;
              cursor: pointer; 

              @include bp-m {
                right: -1rem; top: -1rem;
                width: 2rem;
                height: 2rem;
                font-size: $fs-24;
              }
            }

            &:after {content: none;}

            &:hover:before {
              background: $clr-b1;
              color: white;
              transition: color 0.2s, background-color 0.2s;
            }

            span.name {color: white;}
          }

          a.linkedin {
            display: inline-block;
            @include bp-m {display: flex;}
          }
        }
      }
    }
  }
}

// articles
// * provides white grid border effect whilst giving the list a background colour 

ol.articles {
  @include bp-m {background: rgba($clr-4,0.5);}// *

  li.col {

    @include bp-m {
      box-shadow: 0 0 0 2px white, 0 4px 0 0 white;// *
      &:last-child {box-shadow: 0 0 0 2px white, inset -2px 0 0 0 white;}// *
    }

    a {
      position: relative;
      display: block;
      background: $clr-4;
      text-decoration: none;
      color: $clr-1;
      transition: background-color 0.2s, color 0.2s;

      &:after {
        position: absolute;
        bottom: 0; left: 50%;
        display: inline-block;
        margin: 0 0 0 -3.5rem;
        border-radius: 0.25rem 0.25rem 0 0;
        padding: 0.2rem 0 0.1rem;
        width: 7rem;
        background: white;
        text-align: center;
        font-size: $fs-18;
        color: $clr-b1;
        content: "Read more";
        transition: opacity 0.2s;
      }

      @include bp-m {
        &:after {opacity: 0;}

        &:hover,
        &:focus {
          background: $clr-3;
          &:after {opacity: 1;}
        }
      }

      figure {
        position: relative;
        z-index: 1;
        margin-bottom: 0;

        &:before { // stops page jump on slow loading of images
          position: relative;
          z-index: 1;
          display: block;
          padding-top: 56.3%;
          background: $clr-3;
          content: "";
        }

        img {
          position: absolute;
          top: 0; left: 0;
          z-index: 2;
        }
      }

      div.text {
        padding: $spacing;
        padding-bottom: $spacing*2;

        h3 {
          @extend .h4;
          margin-bottom: $vmargin/2;
          font-weight: 400;
          color: $clr-b0;
        }

        p {
          margin-bottom: 0;
          color: $clr-1;

          time {font-size: $fs-14;}
        }
      }
    }
  }
}

// share options

ul.share-options {
  margin-bottom: 0;
  font-size: $fs-20;

  li {
    display: inline-block;
    margin-right: 0.25rem;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 3rem;
      width: 1.7rem;
      height: 1.7rem;
      text-decoration: none;
      text-align: center;
      color: white;
      transition: box-shadow 0.1s;

      &.icon-linkedin {background: #0077B5;}
      &.icon-twitter {background: #55ADEE;}
      &.icon-facebook {background: #4267B2;}
      &.icon-envelope {background: #7D7D7D;}

      @include bp-m {
        &:hover,
        &:focus {box-shadow: inset 0 0 0 4rem rgba(black,0.1);}
      }

      span {@include hide-screen();}
    }

    &:last-child {margin-right: 0;}
  }
}
